.image {
  -o-object-fit: cover;
     object-fit: cover;
  height: 100%;
}

.translucent {
  opacity: 0.7;
  -webkit-transition: all 300ms ease-out;
  -o-transition: all 300ms ease-out;
  transition: all 300ms ease-out;
}

.translucent:hover {
  opacity: 1;
}



