.work {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
}

.work-outer {
  width: 100%;
  overflow: hidden;
}

.work-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
  height: calc(100vh - 10px);
  padding: 375px 60px;
  position: relative;
  background-color: #202321;
  cursor: pointer;
  overflow: hidden;
}

@media only screen and (max-width : 768px) {
  .work-item {
    height: auto;
    padding: 150px 15px 30px;
  }
}

.link {
  text-decoration: none;
  overflow: hidden;
}

.image {
  position: absolute;
  height: 102%;
  top: 0;
  border: none;
  left: 0;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.25;
  -webkit-transition: opacity 400ms ease-out, -webkit-filter 400ms ease-out, -webkit-transform 400ms ease-out;
  transition: opacity 400ms ease-out, -webkit-filter 400ms ease-out, -webkit-transform 400ms ease-out;
  -o-transition: opacity 400ms ease-out, filter 400ms ease-out, transform 400ms ease-out;
  transition: opacity 400ms ease-out, filter 400ms ease-out, transform 400ms ease-out;
  transition: opacity 400ms ease-out, filter 400ms ease-out, transform 400ms ease-out, -webkit-filter 400ms ease-out, -webkit-transform 400ms ease-out;
}

.work-item:hover .image {
  opacity: 0.5;
  -webkit-transform: translateY(-1.5%);
      -ms-transform: translateY(-1.5%);
          transform: translateY(-1.5%);
}

.text {
  position: relative;
  z-index: 100;
  -webkit-transition: -webkit-transform 300ms ease-out;
  transition: -webkit-transform 300ms ease-out;
  -o-transition: transform 300ms ease-out;
  transition: transform 300ms ease-out;
  transition: transform 300ms ease-out, -webkit-transform 300ms ease-out;
}

.text p,
.text h3 {
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2)
}



