.home {
  height: 100vh;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  position: relative;
}

.hero-image {
  height: 100vh;
  position: relative;
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.hero-accent {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: calc((1/7) * (100vw - 50px));
  background-color: #6B7F5B;
  z-index: 100;
  mix-blend-mode: multiply;
}

@media only screen and (max-width : 768px) {
  .hero-accent {
    opacity: 0;
  }
}

.buttons {
  display: block;
  margin-top: 40px;
}

@media only screen and (max-width : 768px) {
  .about-button {
    display: none;
  }
}

.content-container {
  position: relative;
  z-index: 100;
}

.background-container {
  overflow: hidden;
  left: calc((3/7) * (100vw - 50px));
  position: absolute;
  top: 0;
  right: 0;
}

@media only screen and (max-width : 768px) {
  .background-container {
    left: 0;
    opacity: 0.6;
  }
}



